import React, { FC, forwardRef, memo } from 'react';
import cn from 'classnames';

import { Label } from '../Label';

import { TextAreaProps } from './TextArea.types';

const RCN = 'thuiTextArea';

export const TextArea: FC<TextAreaProps> = memo(
  forwardRef<HTMLTextAreaElement, TextAreaProps>(
    (
      {
        label,
        id,
        name,
        maxlength,
        showLength,
        rows,
        cols,
        placeholder,
        resize,
        disabled,
        invalid,
        required,
        className,
        onChange,
        value,
        ...restProps
      }: TextAreaProps,
      ref
    ) => {
      const textAreaCnString = cn(RCN, className, {
        [`${RCN}--invalid`]: invalid,
        [`${RCN}--resize-${resize}`]: resize,
        [`${RCN}--filled`]: !!value
      });

      return (
        <div className={textAreaCnString}>
          <textarea
            ref={ref}
            className={`${RCN}__textarea`}
            id={id}
            name={name}
            rows={rows}
            cols={cols}
            value={value}
            maxLength={maxlength}
            placeholder={placeholder}
            disabled={disabled}
            required={required}
            onChange={onChange}
            {...restProps}
          />
          {label && (
            <Label label={label} htmlFor={name} isRequired={required} className={`${RCN}__label`} disabled={disabled} />
          )}
          {showLength && (
            <div className={`${RCN}__length`}>
              {value?.toString().length}
              {maxlength ? `/${maxlength}` : ''}
            </div>
          )}
        </div>
      );
    }
  )
);
